import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Formik, Form, Field } from 'formik';
import getCookie from 'js-cookie'
import { event } from 'jquery';

axios.defaults.xsrfCookieName = 'csrftoken'
axios.defaults.xsrfHeaderName = 'X-CSRFToken'

const csrftoken = getCookie('csrftoken');

function LabDetail({ match }) {

    const [lab, setLab] = useState({})
    const [course_lab, setCourse_lab] = useState([])
    const [course, setCourse] = useState([])
    const id = match.params.id

    useEffect(() => {
        axios({
            method: "GET",
            url: `http://127.0.0.1:8000/api/labs/${id}/`
        }).then(response => {
            setLab(response.data)
            setCourse_lab(response.data.course_lab)
            setCourse(response.data.course_lab.course)
        })
    }, [id])

    return (
        <div>
            <h4><strong>{course_lab.name}</strong></h4>
            <h6><strong>Курс:</strong> {course.name}</h6>
            <p><strong>Задание:</strong> {course_lab.task}</p>
            <p><strong>Выдана:</strong> {lab.issued}</p>
            <p><strong>Выполнена:</strong> {lab.completed}</p>
            <p><strong>Отчет:</strong> {lab.report}</p>

            <Formik
                initialValues={{ report: '' }}
                validate={() => { }}
                onSubmit={values => {
                    let data = new FormData();
                    data.append('report', values.report)
                    alert(JSON.stringify({
                        "id": lab.id,
                        "report": data,
                        "issued": lab.issued,
                        "completed": lab.completed,
                        "changed": lab.changed,
                        "student": lab.student,
                        "course_lab": lab.course_lab.id
                    }, null, 2));
                    axios({
                        method: 'put',
                        url: `http://127.0.0.1:8000/api/deflabs/${id}/`,
                        data: {
                            "id": lab.id,
                            "report": data,
                            "issued": lab.issued,
                            "completed": lab.completed,
                            "changed": lab.changed,
                            "student": lab.student,
                            "course_lab": lab.course_lab.id
                        }
                    })
                }}
            >
                <Form>
                    <div class="mb-3">
                        <label class="form-label" for="report">Добавить отчет:</label>
                        <br />
                        <Field type="file" name="report" />
                    </div>
                    <button class="btn btn-primary" type="submit" >Добавить</button>
                </Form>
            </Formik>
        </div>
    );
}

export default LabDetail;